<template>
  <div class="dominioPresenca">
      <v-container>
        <v-layout row wrap>
          <v-flex sm12 xs12 md12 lg12 class="mt-10">
              <v-card-title class="align-start">
                <v-sheet
                color="brown"
                width="100%"
                class="overflow-hidden mt-n9 transition-swing v-card--material__sheet"
                style="z-index: 2"
                elevation="6"
                max-width="100%"
                rounded
                >
                  <v-theme-provider dark>
                  <slot name="heading" />
                  <div class="py-3">
                    <v-row class="px-5 d-flex">
                      <v-col
                        cols="1"
                        sm="1"
                        md="1"
                        lg="1"
                        xs="1"
                        class="text-left"
                      >
                        <v-btn
                          depressed
                          color="transparent"
                          class="text-left"
                          @click="returnHome"
                        >
                          <v-icon dark small>
                            mdi-arrow-left-bold-circle mdi-36px
                          </v-icon>
                        </v-btn>
                      </v-col>
                      <v-col
                        cols="11"
                        sm="11"
                        md="11"
                        lg="11"
                        xs="11"
                        class="text-center"
                      >
                        <span class="text-h7 white--text  v-card--material__title pr-15">Dominio Presena</span>
                      </v-col>
                    </v-row>
                  </div>
                </v-theme-provider>
                </v-sheet>
              </v-card-title>
              <v-card class="card-funcao">
                <v-container class="py-0">
                  <v-row class="pt-9 px-5">
                    <template>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="5"
                        xs="5"
                      >
                        <v-text-field
                        v-model="textoPesquisar"
                          clearable
                          label="Pesquisar"
                          append-icon="mdi-magnify"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:right"
                      >
                        <v-btn color="blue-grey" class="white--text" @click="getDominio()">Pesquisar</v-btn>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="5"
                        xs="5"
                        style="text-align:right"
                      >
                        <v-btn color="blue" class="white--text" @click="adicionarDominio('adicionar')">+ Dominio Presena</v-btn>
                      </v-col>
                    </template>
                  </v-row>
                  <v-row>
                    <template>
                      <v-col
                        cols="12"
                        md="12"
                        lg="12"
                        sm="12"
                        xs="12"
                      >
                        <template>
                          <v-data-table
                            :headers="headers"
                            :items="desserts"
                            :items-per-page="5"
                            class="elevation-1 color-table"
                            :loading="loadingTable"
                          >
                          <template v-slot:[`item.deletar`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              class="mr-5"
                              fab
                              dark
                              small
                              color="red"
                              @click="deletarDominio(item)"
                            >
                            <v-icon dark small>
                              mdi-close
                            </v-icon>{{ item.deletar }}
                            </v-btn>
                          </template>
                          <template v-slot:[`item.editar`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              class="mr-5"
                              fab
                              dark
                              small
                              color="primary"
                              @click="editarDominio('editar', item)"
                            >
                            <v-icon dark small>
                              mdi-pencil
                            </v-icon>{{ item.editar }}
                            </v-btn>
                          </template>
                          </v-data-table>
                        </template>
                      </v-col>
                      </template>
                  </v-row>
                </v-container>
              </v-card>
          </v-flex>
        </v-layout>

        <v-dialog
          v-model="dialogDominioPresenca"
          transition="dialog-top-transition"
          max-width="600"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >{{tipoDominio === 'adicionar' ? 'Adicionar' : 'Editar'}} Dominio Presena</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                      <v-text-field
                        v-model="novoDominio"
                        :rules="rules.name"
                        label="Nome do Dominio"
                        required
                        clearable
                      ></v-text-field>
                      </v-col>
                    </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialogDominioPresenca = false"
                >Fechar</v-btn>
                <v-btn
                  color="green"
                  text
                  @click="verificarFormulario()"
                >Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-snackbar
          v-model="snackbar"
          :color="colorSnackbar"
        >
          {{ mensagem }}

          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbar = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-container>
      <div v-if="loader">
        <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" name="circular"></loader>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'DominoPresenca',
  data: () => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    loader: false,
    novoDominio: '',
    snackbar: false,
    dialogDominioPresenca: false,
    mensagem: '',
    colorSnackbar: '',
    textoPesquisar: '',
    desserts: [],
    tipoDominio: '',
    nomeAntigo: '',
    headers: [
      {
        text: 'Dominio',
        align: 'start',
        sortable: false,
        value: 'nome'
      },
      {
        text: 'Editar',
        value: 'editar',
        align: 'right',
        class: 'class-btn'
      },
      {
        text: 'Deletar',
        value: 'deletar',
        align: 'right',
        class: 'class-btn'
      }
    ],
    loadingTable: true,
    rules: {
      name: [val => (val || '').length > 0 || 'Preencher o Nome do Dominio']
    }
  }),
  created () {
    this.getDominio()
  },
  methods: {
    returnHome () {
      this.$router.push({ name: 'Home' })
    },
    async getDominio () {
      this.loadingTable = true

      const options = {
        url: `${this.urlProd}dominio-presenca`,
        method: 'POST',
        data: { textoPesquisar: this.textoPesquisar ? this.textoPesquisar : '' }
      }
      axios(options)
        .then(res => {
          this.desserts = res.data
          console.log(this.desserts)
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = err
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingTable = false
        })
    },

    async verificarFormulario () {
      if (this.novoDominio) {
        this.loader = true

        const url = this.tipoDominio === 'adicionar' ? `${this.urlProd}novo-dominio-presenca` : `${this.urlProd}editar-dominio-presenca`
        const params = this.tipoDominio === 'adicionar' ? { novoDominio: this.novoDominio }
          : { novoDominio: this.novoDominio, nomeAntigo: this.nomeAntigo }

        const options = {
          method: 'POST',
          url: url,
          data: params
        }
        axios(options)
          .then(res => {
            if (res.data.qtd > 0) {
              this.snackbar = true
              this.mensagem = res.data.mensagem
              this.colorSnackbar = 'red'
            } else {
              this.snackbar = true
              this.mensagem = res.data.mensagem
              this.colorSnackbar = 'green'
              this.dialogDominioPresenca = false
              this.novoDominio = ''
              this.getDominio()
            }
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = err
            this.colorSnackbar = 'red'
          })
          .finally(() => {
            this.loader = false
          })
      } else {
        this.snackbar = true
        this.mensagem = 'Preencha o Campo Nome do Dominio'
        this.colorSnackbar = 'red'
      }
    },

    adicionarDominio (tipo) {
      this.dialogDominioPresenca = true
      this.tipoDominio = tipo
    },

    editarDominio (tipo, item) {
      this.dialogDominioPresenca = true
      this.tipoDominio = tipo
      this.novoDominio = item.nome
      this.nomeAntigo = item.nome
    },

    async deletarDominio (nome) {
      this.loader = true

      const options = {
        method: 'POST',
        url: `${this.urlProd}delete-dominio-presenca`,
        data: { nomeDominio: nome }
      }
      axios(options)
        .then(res => {
          if (res.status === 500) {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'red'
            this.dialogDominioPresenca = false
          } else {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.dialogDominioPresenca = false
            this.getDominio()
          }
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = err
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loader = false
        })
    }
  }
}
</script>

